import h5Copy from '@/components/junyi-h5-copy.js'
export default {
	data() {
		return {
			uni:'',
			cartInfo: [],
			userInfo:[],
			maskState: 0,
			now_money:0,
		}
	},
	onLoad(option) {
		//商品数据
		console.log('啊啊啊啊', option);
		getApp().user_api(true);
		this.id = option.id
		
	},
	onShow() {
		this.confirm_order();
	},
	methods: {
		confirm_order() {
			let self = this;
			let path = '/gzh/shop_api/order_detail'
			let data = {
				id: self.id
			}
			getApp().http(path, data, function(res) {
				uni.hideLoading();
				self.cartInfo = res;
				// self.userInfo = getApp().globalData.userInfo
				// self.now_money = getApp().globalData.userInfo.now_money
				
				console.log('水电费水电费',self.cartInfo);
			})
		},
		
		orderpj(unique){
			uni.navigateTo({
				url: "/pages/order/orderEval?unique="+unique
			})

		},
		
		// 确认收货
		orderCon(){
			let self = this;
			uni.showModal({
				title: '确认收货',
				content: '为保障权益，请收到货确认无误后，再确认收货',
				success: (md) => {
					console.log('sssss')
					if(md.confirm == true){
						let path = '/gzh/shop_api/user_take_order'
						let data = {
							id: self.id
						}
						getApp().http(path, data, function(res) {
							uni.hideLoading();
							if(res.code == 200){
								getApp().showTip(res.msg, "success")
								self.confirm_order();
							}else{
								getApp().showTip(res.msg, "none")
							}
						},true)
					}
				}
			})
			
			
			
		},
		
		copy() {
			let content = this.cartInfo.delivery_id 
			const result = h5Copy(content)
			if (result === false) {
				uni.showToast({
					title:'不支持',
				})
			} else {
				uni.showToast({
					title:'复制成功',
					icon:'none'
				})
			}
		},
		
		// 再次购买
		goOrderConfirm(){
			let self = this;
			let path = '/gzh/user_api/get_order'
			let data = {
				uni: self.uni
			}
			getApp().http(path, data, function(res) {
				
				let cartArr = res.cart_id
				let cartId = cartArr.toString();
				console.log(res,cartId);
				
				uni.navigateTo({
					url: "/pages/order/createOrder?cartId="+res.cart_id
				})
			})
		},
		
		// 删除订单
		delOrder(){
			
			uni.showModal({
				title: '删除',
				content: '确定删除此订单吗？',
				success: (md) => {
					console.log('sssss')
					uni.showLoading()
					if(md.confirm == true){
						let self = this;
						let path = '/gzh/shop_api/order_del'
						let data = {
							id: self.id
						}
						getApp().http(path, data, function(res) {
							uni.hideLoading();
							if(res.code == 200){
								uni.showToast({
								    title: res.msg,
								    duration: 1500,
									success(){
										setTimeout(function(){
											uni.navigateBack(3)
										},1500)
									},
								});
							}else{
								getApp().showTip(res.msg, "none")
							}
							
						},true);
					}
				}
			})

			
			
		},
		
		indexUrl(icon) {
			let http = icon.indexOf('http')
			if (http == -1) {
				return this.webUrl + '/' + icon
			} else {
				return icon;
			}
		},
		//显示面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},

		
	}
}
